@import "./assets/sass/theme-colors";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Arimo", sans-serif;
  font-size: 15px;
  margin: 0;
  color: var(--body-color);
  background: var(--body-back);
  transition: 0.2s ease background;
}
body:before {
  content: '';
  background-image: url("./assets/img/material-back.jpg");
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-size: cover;
  height: 100%;
  z-index: -1;
  opacity: 0.1;
}
@media screen and (max-width: 900px) {
  body {
    font-size: 14px;
  }
}
.page {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 3em 0.5em;
}
h1 {
  font: 500 5em "Oswald", sans-serif;
}
h3 {

}
.specialization {
  font: 600 1.5em "Arimo";
  color: var(--acsent);
  margin-bottom: 0.7em;
}
a {
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}
